import React from 'react';
import './NodeModal.css';

const NodeModal = ({ isOpen, onClose, content, onModalClose, description }) => {
    if (!isOpen) return null;
  
    const handleClose = () => {
        onClose();
        if (onModalClose) onModalClose(); // Chama a função para ocultar o botão conteúdo
    };
  
    return (
      <div className="modal-overlay" onClick={handleClose}>
        <div className="modal-content" onClick={(e) => e.stopPropagation()}>
            <button className="modal-close" onClick={handleClose}>×</button>
            <h2 className="modal-title">{content}</h2> {/* Exibe o texto do nó em negrito */}
            {Array.isArray(description) ? (
                <ul className="modal-list">
                    {description.map((item, index) => (
                        <li key={index}>• {item}</li>
                    ))}
                </ul>
            ) : (
                <p>{description}</p>
            )}
        </div>
      </div>
    );
};

export default NodeModal;
